'use strict';

import '../tailwind.js';

// connect tailwind.js to the form and add the class to the form element when the form is submitted
// get all objects from tailwind.js and make input elements




document.querySelector('#tailwind-form').addEventListener('submit', function (event) {
  event.preventDefault();
  document.querySelector('#tailwind-form').classList.add('tailwind');
  console.log('tail');
});
